/* Project uses min-width media query and there are 2 breakpoints:
i.e 750px (medium) & 1200px (large)
*/
.header{
    background-color: white;
    width: 100%;
    min-width: 0 !important;
    box-shadow: 0 0 5px 5px rgb(228, 228, 228);
}

.header__inner{
    
    display: flex;
    flex-direction: column;
}

.header__top{
    background-color: var(--color-primary);
    padding: var(--margin-small) var(--margin-normal);
    color: var(--font-color-lighter);
    height: 50px;
    display: none;
    align-items: center;
    justify-content: flex-end;
}

.header__top__inner{
    justify-content: flex-end;
    width: 100%;
    max-width: 1400px;
}

.header__top__links{
    display: flex;
    justify-content: flex-end;
    padding: 0 var(--margin-small);
}

.header__top__links__inner{
    display: flex;
}

.header__top__links__inner > *:not(:first-child){
    margin-left: var(--margin-normal);
}

.header__nav{
    /*border: 1px solid black;*/
    height: 70px;
    padding: var(--margin-small) var(--margin-normal);
    display: flex;
    justify-content: center;
    align-items: center;

}

.header__nav__inner{
    width: 100%;
    max-width: 1360px;
    display: flex;
    justify-content: space-between;
}

.header__nav__menu-btn{
    display: flex;
    align-items: center;
    width: 40px;
    justify-content: center;
}

.header__nav__menu-btn__inner{
    width: 100%;
    height: 100%;
}

.header__nav__search-box{
    display: none;
}

.header__nav__categories{
    display: none;
}

.header__nav__logo__inner{
    height: 40px;
}

.header__search-bar{
    height: 60px;
    padding: 0 var(--margin-small);
}

@media screen and (min-width: 750px) {

    .header__top{
        display: flex;
    }

    .header__nav{
        height: 80px;
    }

    .header__nav__inner{
        justify-content: initial;
    }

    .header__nav__inner > *:not(.header__nav__logo){
        margin-left: var(--margin-normal);
    }

    .header__nav__search-box{
        display: flex;
        max-width: 800px;
        margin-right: auto;
        flex: 1;
    }

    .header__nav__search-box__inner{
        width: 100%;
    }

    .header__nav__logo{
        order: -1;
    }

    .header__search-bar{
        display: none;
    }
}

@media screen and (min-width: 1100px){
    .header__nav__menu-btn{
        display: none;
    }

    .header__nav__categories{
        display: flex;
        padding: 0 var(--margin-normal);
        align-items: center;
    }

    .header__nav__categories__inner{
        display: flex;
        list-style-type: none;
        font-size: var(--font-size-normal);
    }
    
    .header__nav__categories__inner > *:not(:first-child){
        margin-left: var(--margin-normal);
    }
}